import React from "react";
import DestinationComponentDescr from "./DestinationComponentDescr";
import { v4 as uuidv4 } from "uuid";
import { motion } from "framer-motion";

const DestinationComponent = ({ destinationsData, imageSrc, imgAlt }) => {
  return (
    <div className="destinations-content">
      <div className="destinations-content-image-container">
        <motion.img
          key={uuidv4()}
          animate={{ scale: 1 }}
          initial={{ scale: 0.5 }}
          transition={{ duration: 0.5 }}
          src={imageSrc}
          alt={imgAlt}
        />
      </div>

      <DestinationComponentDescr
        destinationsData={destinationsData}
        //adding a key so each time the state changes the animations gets added
        key={uuidv4()}
      />
    </div>
  );
};

export default DestinationComponent;
