import React, { useState } from "react";
import data from "../data/data.json";
//Importing images
import launchVehicleImg from "../assets/technology/image-launch-vehicle-portrait.jpg";
import spacePortImg from "../assets/technology/image-spaceport-portrait.jpg";
import spaceCapsuleImg from "../assets/technology/image-space-capsule-portrait.jpg";
import { v4 as uuidv4 } from "uuid";
import { motion } from "framer-motion";

const TechnologyContent = () => {
  let technologyShips = data.technology;
  let [launchVehicle, spacePort, spaceCapsule] = technologyShips;
  const [tchShip, setTchShip] = useState(launchVehicle);
  const [imgSrc, setImgSrc] = useState(launchVehicleImg);
  const [imgAlt, setImgAlt] = useState(technologyShips.name);
  //Object that contains the information about vehicles
  const technologyButtons = [
    { id: 1, value: launchVehicle },
    { id: 2, value: spacePort },
    { id: 3, value: spaceCapsule },
  ];
  //If button id is equal tu activeId add className active to the button
  const [activeId, setActiveId] = useState(1);

  const changePageContent = (ship) => {
    //Object that contains all the images to display them
    const images = {
      launchvehicleImg: launchVehicleImg,
      spaceportImg: spacePortImg,
      spacecapsuleImg: spaceCapsuleImg,
    };
    const imageSrcName = ship.name.split(" ").join("").toLowerCase() + "Img";
    setImgSrc(images[imageSrcName]);
    setTchShip(ship);
    setImgAlt(ship.name);
  };
  return (
    <motion.div
      animate={{ opacity: 1 }}
      initial={{ opacity: 0 }}
      transition={{ duration: 1 }}
      className="technology-content-container"
    >
      <div className="technology-content">
        <div className="technology-container-content-buttons">
          {technologyButtons.map((button) => {
            return (
              <button
                key={uuidv4()}
                className={button.id === activeId ? "technology-active" : ""}
                onClick={() => {
                  changePageContent(button.value);
                  setActiveId(button.id);
                }}
              >
                {button.id}
              </button>
            );
          })}
        </div>
        <motion.div
          key={uuidv4()}
          animate={{ opacity: 1, y: 0 }}
          initial={{ y: 250, opacity: 0 }}
          transition={{ duration: 0.7 }}
          className="technology-container-content-text"
        >
          <h4>the terminology...</h4>
          <h3>{tchShip.name}</h3>
          <p>{tchShip.description}</p>
        </motion.div>
      </div>
      <div className="image-container">
        <motion.img
          key={uuidv4()}
          animate={{ opacity: 1, y: 0 }}
          initial={{ y: -250, opacity: 0 }}
          transition={{ duration: 0.7 }}
          src={imgSrc}
          alt={imgAlt}
        />
      </div>
    </motion.div>
  );
};

export default TechnologyContent;
