import React from "react";
import { motion } from "framer-motion";
import { v4 as uuidv4 } from "uuid";

const CrewDescription = ({ crewMember }) => {
  return (
    <motion.div
      animate={{ x: 0 }}
      initial={{ x: "-100" }}
      transition={{ type: "spring", stiffness: 100 }}
      key={uuidv4()}
      className="crew-container-content-description"
    >
      <h4>{crewMember.role}</h4>
      <h3>{crewMember.name}</h3>
      <p>{crewMember.bio}</p>
    </motion.div>
  );
};

export default CrewDescription;
