import React from "react";
import { motion } from "framer-motion";

const DestinationComponentDescr = ({ destinationsData }) => {
  return (
    <motion.div
      animate={{ opacity: 1 }}
      initial={{ opacity: 0 }}
      transition={{ duration: 1.2 }}
      className="destinations-content-description"
    >
      <motion.h2 animate={{ y: 0 }} initial={{ y: -250 }}>
        {destinationsData.name}
      </motion.h2>
      <p>{destinationsData.description}</p>
      <div className="destinations-line"></div>
      <div className="details">
        <div className="distance">
          <h5>Avg.Dinstance</h5>
          <h4>{destinationsData.distance}</h4>
        </div>
        <div className="travel-time">
          <h5>Est. Travel Time</h5>
          <h4>{destinationsData.travel}</h4>
        </div>
      </div>
    </motion.div>
  );
};

export default DestinationComponentDescr;
