import React, { useState } from "react";
import data from "../data/data.json";
import CrewDescription from "./CrewDescription";

//Importing images
import douglasImage from "../assets/crew/image-douglas-hurley.webp";
import markImage from "../assets/crew/image-mark-shuttleworth.webp";
import victorImage from "../assets/crew/image-victor-glover.webp";
import anoushehImage from "../assets/crew/image-anousheh-ansari.webp";
import { motion } from "framer-motion";
import { v4 as uuidv4 } from "uuid";

const Crew = () => {
  //extracting the data out of the data.json
  let crewData = data.crew;
  let [douglas, mark, victor, anousheh] = crewData;
  //creating states to use and change the page content
  const [crewMember, setCrewMember] = useState(douglas);
  const [imgSrc, setImgSrc] = useState(douglasImage);
  const [imgAlt, setImgAlt] = useState("douglas");
  //buttons array to map through and create buttons on the page
  const buttons = [
    { id: 0, text: douglas },
    { id: 1, text: mark },
    { id: 2, text: victor },
    { id: 3, text: anousheh },
  ];
  const [activeId, setActiveId] = useState(0);

  const changeCrewMemberHandler = (crewMem, e) => {
    //refference to the images(not the path to them)
    const images = {
      douglasImg: douglasImage,
      markImg: markImage,
      victorImg: victorImage,
      anoushehImg: anoushehImage,
    };

    //getting the image name from the object
    let imageSrcName = crewMem.name.split(" ")[0].toLowerCase();
    let imgSrcName = imageSrcName + "Img";
    //changing the content
    setCrewMember(crewMem);
    setImgAlt(crewMem.name);
    setImgSrc(images[imgSrcName]);
  };

  //variants for animating
  const containerVariants = {
    hidden: {
      opacity: 0,
      x: "100vw",
    },
    visible: {
      opacity: 1,
      x: 0,
      transition: {
        type: "spring",
        delay: 0.5,
      },
    },
    exit: {
      x: "-100vw",
      transition: { ease: "easeInOut" },
    },
  };
  return (
    <div className="crew">
      <h1>
        <span>02</span> meet your crew
      </h1>
      <motion.div
        variants={containerVariants}
        initial="hidden"
        animate="visible"
        exit="exit"
        className="crew-container"
      >
        <div className="crew-container-content">
          <CrewDescription crewMember={crewMember} />
          <div className="buttons">
            {buttons.map((button) => {
              return (
                <button
                  key={button.id}
                  onClick={(e) => {
                    changeCrewMemberHandler(button.text, e);
                    setActiveId(button.id);
                  }}
                  className={activeId === button.id ? "crew-active" : ""}
                ></button>
              );
            })}
            {/* <button
              onClick={(e) => {
                changeCrewMemberHandler(douglas, 1);
                classListCheck(e);
              }}
              className={activeId === values ? "crew-active" : ""}
            ></button>
            <button
              className={activeId === 2 ? "crew-active" : ""}
              onClick={(e) => {
                changeCrewMemberHandler(mark, 2);
                classListCheck(e);
              }}
            ></button>
            <button
              className={activeId === 3 ? "crew-active" : ""}
              onClick={(e) => {
                changeCrewMemberHandler(victor, 3);
                classListCheck(e);
              }}
            ></button>
            <button
              className={activeId === 4 ? "crew-active" : ""}
              onClick={(e) => {
                changeCrewMemberHandler(anousheh, 4);
                classListCheck(e);
              }}
            ></button> */}
          </div>
        </div>
        <div className="crew-container-image">
          <motion.img
            animate={{ opacity: 1, y: 0 }}
            initial={{ opacity: 0, y: "-50vh" }}
            transition={{ type: "spring", stiffness: 100 }}
            exit={{ opacity: 0, y: "50vh" }}
            key={uuidv4()}
            src={imgSrc}
            alt={imgAlt}
          />
        </div>
      </motion.div>
    </div>
  );
};

export default Crew;
