import React from "react";
import { NavLink } from "react-router-dom";
import closeIcon from "../assets/shared/icon-close.svg";

const NavLinks = ({ displayNavbar, setDisplayNavbar }) => {
  return (
    <div className={`nav-links ${displayNavbar ? "nav-links-display" : ""}`}>
      <button
        onClick={() => setDisplayNavbar((prevDisp) => !prevDisp)}
        className="close-menu-icon toggle-button"
      >
        <img src={closeIcon} alt="close-menu-icon" />
      </button>
      <ul>
        <li>
          <NavLink onClick={() => setDisplayNavbar(false)} to="/">
            <span className="page-number">00</span> home
          </NavLink>
        </li>
        <li>
          <NavLink onClick={() => setDisplayNavbar(false)} to="/destinations">
            <span className="page-number">01</span> Destination
          </NavLink>
        </li>
        <li>
          <NavLink onClick={() => setDisplayNavbar(false)} to="/crew">
            <span className="page-number">02</span>
            Crew
          </NavLink>
        </li>
        <li>
          <NavLink onClick={() => setDisplayNavbar(false)} to="/technology">
            <span className="page-number">03</span> Technology
          </NavLink>
        </li>
      </ul>
    </div>
  );
};

export default NavLinks;
