import React, { useState } from "react";
import data from "../data/data.json";
import DestinationComponent from "./DestinationComponent";
//Import images
import moonImage from "../assets/destination/image-moon.webp";
import marsImage from "../assets/destination/image-mars.webp";
import europaImage from "../assets/destination/image-europa.webp";
import titanImage from "../assets/destination/image-titan.webp";
import { motion } from "framer-motion";

const Destinations = () => {
  let { destinations } = data;
  let [moon, mars, europa, titan] = destinations;
  let [imageSrc, setImageSrc] = useState(moonImage);
  const [content, setContent] = useState(moon);
  const [imgAlt, setImgAlt] = useState("moon");
  const [selected, setSelected] = useState(1);

  const containerVariants = {
    hidden: {
      opacity: 0,
      x: "100vw",
    },
    visible: {
      opacity: 1,
      x: 0,
      transition: {
        type: "spring",
        delay: 0.5,
      },
    },
    exit: {
      x: "-100vw",
      transition: { ease: "easeInOut", duration: 1 },
    },
  };
  //function handler
  const changeComponentHandler = (destination, destinationNumber) => {
    // if (event.target.textContent === "moon") {
    //   setContent(moon);
    //   setImageSrc(moonImage);
    //   setImgAlt("moon");
    // } else if (event.target.textContent === "mars") {
    //   setContent(mars);
    //   setImageSrc(marsImage);
    //   setImgAlt("mars");
    // } else if (event.target.textContent === "europa") {
    //   setContent(europa);
    //   setImageSrc(europaImage);
    //   setImgAlt("europa");
    // } else if (event.target.textContent === "titan") {
    //   setContent(titan);
    //   setImageSrc(titanImage);
    //   setImgAlt("titan");
    // }
    //Images reference
    const destinationImages = {
      moonImg: moonImage,
      marsImg: marsImage,
      europaImg: europaImage,
      titanImg: titanImage,
    };
    //Getting the image name from the obj
    const imageName = destination.name.toLowerCase();
    let imgRef = imageName + "Img";
    //Changing the content
    setContent(destination);
    setImageSrc(destinationImages[imgRef]);
    setImgAlt(destination.name);
    setSelected(destinationNumber);
  };
  return (
    <div className="destinations">
      <motion.div
        variants={containerVariants}
        initial="hidden"
        animate="visible"
        exit="exit"
        className="destinations-container"
      >
        <h1>
          <span>01</span> pick your destination
        </h1>
        <div className="destinations-container-content">
          <div className="destinations-navbar">
            {/* //I know i couldve make an array that keeps 4 li items and to loop through it , as i did in the other components , i just wanted to let this here to show that i refractored some of the code :) */}
            <ul>
              <motion.li
                animate={{ x: 0 }}
                initial={{ x: -500 }}
                className={selected === 1 ? "destinations-active" : ""}
                onClick={() => changeComponentHandler(moon, 1)}
              >
                moon
              </motion.li>
              <motion.li
                animate={{ x: 0 }}
                initial={{ x: -500 }}
                className={selected === 2 ? "destinations-active" : ""}
                onClick={() => changeComponentHandler(mars, 2)}
              >
                mars
              </motion.li>
              <motion.li
                animate={{ x: 0 }}
                initial={{ x: -500 }}
                className={selected === 3 ? "destinations-active" : ""}
                onClick={() => changeComponentHandler(europa, 3)}
              >
                europa
              </motion.li>
              <motion.li
                animate={{ x: 0 }}
                initial={{ x: -500 }}
                className={selected === 4 ? "destinations-active" : ""}
                onClick={() => changeComponentHandler(titan, 4)}
              >
                titan
              </motion.li>
            </ul>
          </div>
          <DestinationComponent
            imageSrc={imageSrc}
            destinationsData={content}
            imgAlt={imgAlt}
          />
        </div>
      </motion.div>
    </div>
  );
};

export default Destinations;
